// Exercice 4

// Ã‰couter le click de l'utilisateur sur les boutons "+" et "-"
// Ã€ chaque click vous augmenterez / diminuerez une variable contenant un chiffre et l'afficherez dans la console

const buttonPlus: HTMLElement | null = document.getElementById('incrementButton')
const buttonMoins: HTMLElement | null = document.getElementById('decrementButton')

let count: number = 0

function incrementCount() {
    count ++
    console.log(count)
}

function decrementCount() {

    if (count > 0) {
     count --
    }

    console.log(count)
}

if (buttonPlus && buttonMoins) {
    buttonPlus.addEventListener('click', incrementCount)
    buttonMoins.addEventListener('click', decrementCount)
}

