// Remplacer le paragraph contenant "Remplacer par la saisie de l'utilisateur" par la valeur de l'input en direct

const domExoFive: HTMLElement | null = document.getElementById('inputTextExo5')
const exoCinquUn: HTMLElement | null = document.getElementById('replaceMe')

if (domExoFive !== null) {
    domExoFive.addEventListener('input', handleInputChanges);
}

function handleInputChanges(event: Event) {
    if (exoCinquUn !== null) {
        exoCinquUn.innerText = event.target?.value
    }
}
